import React, { useContext } from 'react';
import { Auth } from 'aws-amplify';
import _get from 'lodash/get';
// material-ui
import {
  Card,
  CardContent,
  Chip,
  ClickAwayListener,
  Divider,
  Grid,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Paper,
  Popper,
  makeStyles,
  Typography
} from '@material-ui/core';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';

import Transitions from 'components/Shared/Transitions';
import UserContext from 'contexts/UserContext';
import style from '../headerStyle';
import SwitcherPlanCard from './SwitcherPlanCard';

const useStyles = makeStyles(style);

const RoleSwitcherSection = () => {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const prevOpen = React.useRef(open);
  const anchorRef = React.useRef(null);

  const USER_MODE = {
    seller: 'Seller Mode',
    buyer: 'Buyer Mode',
  };
  const ROLES = Object.keys(USER_MODE);
  const userRole = useContext(UserContext);
  const email = _get(Auth, ['user', 'attributes', 'email'], '');
  const isBuyer = _get(userRole, 'IsBuyer', false);
  const userRoleValues = _get(userRole, 'values', {});

  const authentication = {
    username: email,
    selectedRole: ROLES[Number(isBuyer)],
    roles: userRoleValues,
    switchMode: {
      seller: userRole.switchToBuyerMode,
      buyer: userRole.switchToSellerMode
    }
  };

  React.useEffect(() => {
    if (prevOpen.current === true && open === false) {
      anchorRef.current.focus();
    }

    prevOpen.current = open;
  }, [open]);

  const handleToggle = () => {
    setOpen((prevOpen) => !prevOpen);
  };

  const handleClose = (event) => {
    if (anchorRef.current && anchorRef.current.contains(event.target)) {
      return;
    }

    setOpen(false);
  };

  const handleLogout = async () => {
    await Auth.signOut();
  };

  return (
    <React.Fragment>
      <Chip
        className={classes.roleSwitcherChip}
        icon={
          <AccountCircleIcon
            stroke={1.5}
            size="1.5rem"
            className={classes.headerAvatar}
            ref={anchorRef}
            aria-controls={open ? 'menu-list-grow' : undefined}
            aria-haspopup="true"
            color="inherit"
          />
        }
        label={USER_MODE[authentication.selectedRole]}
        variant="outlined"
        ref={anchorRef}
        aria-controls={open ? 'menu-list-grow' : undefined}
        aria-haspopup="true"
        onClick={handleToggle}
        color="primary"
      />
      <Popper
        placement="bottom-end"
        open={open}
        anchorEl={anchorRef.current}
        role={undefined}
        transition
        disablePortal
        popperOptions={{
          modifiers: [
            {
              name: 'offset',
              options: {
                offset: [0, 14]
              }
            }
          ]
        }}
      >
        {({ TransitionProps }) => (
          <Transitions in={open} {...TransitionProps}>
            <Paper>
              <ClickAwayListener onClickAway={handleClose}>
                <Card className={classes.cardContent}>
                  <CardContent >
                    <Grid container direction="column" spacing={0}>
                      <Grid item className={'flex'}>
                        <Typography component="span" variant="subtitle1" className={classes.username}>
                          {authentication.username}
                        </Typography>
                        <Typography variant="subtitle2" className={classes.switcherRole}>
                          {USER_MODE[authentication.selectedRole]}
                        </Typography>
                      </Grid>
                    </Grid>

                    <Divider />

                    <SwitcherPlanCard
                      witchMode={authentication.switchMode}
                      selectedRole={authentication.selectedRole}
                      roles={authentication.roles}
                    />

                    <List component="nav" className={classes.navContainer}>
                      <ListItem
                        className={classes.menuItem}
                        button
                        onClick={handleLogout}
                      >
                        <ListItemIcon>
                          <ExitToAppIcon stroke={1.5} size="1.3rem" />
                        </ListItemIcon>
                        <ListItemText primary={<Typography variant="body2">Logout</Typography>} />
                      </ListItem>
                    </List>
                  </CardContent>
                </Card>
              </ClickAwayListener>
            </Paper>
          </Transitions>
        )}
      </Popper>
    </React.Fragment>
  );
};

export default RoleSwitcherSection;

